<template>
  <div style="padding: 10px">
    <div style="margin: 10px 0 ;display: flex">
      <el-button type="primary" @click="open">权限等级说明</el-button>
      <el-button type="primary" @click="adduser">新增用户</el-button>
<!--      <el-button type="primary">导入</el-button>-->
<!--      <el-button type="primary">导出</el-button>-->
    </div>
<!--    <div style="margin: 10px 0 ;display: flex">-->
<!--      <el-input v-model="search" placeholder="请输入内容" style="width: 20%" clearable/>-->
<!--      <el-button type="primary" style="margin-left: 5px">查询</el-button>-->
<!--    </div>-->
   <el-table :data="tableData" stripe style="width: 100%" border>
     <el-table-column prop="id" label="ID" width="150" sortable/>
     <el-table-column prop="name" label="用户名" width="300" />
     <el-table-column prop="phone" label="联系电话" width="300" />
     <el-table-column prop="level" label="权限等级" width="300" />
     <el-table-column fixed="right" label="操作" width="169">
       <template #default="scope">
         <el-button type="text" size="small" @click="handleEdit(scope.row)"
         >修改权限</el-button
         >
<!--         <el-button type="text" size="small" @click="handleEdit2(scope.row)"-->
<!--         >编辑</el-button-->
<!--         >-->
         <el-popconfirm title="确定删除吗?" @confirm="handleDelete(scope.row.id)">
           <template #reference>
             <el-button type="text" size="small">删除</el-button>
           </template>
         </el-popconfirm>
       </template>
     </el-table-column>
   </el-table>
    <div style="margin: 10px 0">
      <el-pagination
          v-model:currentPage="currentPage"
          v-model:page-size="pageSize"
          :page-sizes="[5, 10, 15, 20]"
          :small="0"
          :disabled="false"
          :background="true"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      />
    </div>
    <el-dialog
        v-model="dialogVisible"
        title="修改权限"
        width="30%"
        @close="closeAddDialog"
    >
      <el-form :model="form" label-width="120px">
        <el-form-item label="权限等级" style="width: 80%">
          <el-radio v-model="form.level" label="1" size="large">超级管理员</el-radio>
          <el-radio v-model="form.level" label="2" size="large">产品管理员</el-radio>
          <el-radio v-model="form.level" label="3" size="large">质检员</el-radio>
          <el-radio v-model="form.level" label="4" size="large">普通用户</el-radio>
<!--          <el-radio v-model="form.level" label="4" size="large">芯片管理员</el-radio>-->
<!--          <el-radio v-model="form.level" label="5" size="large">普通用户</el-radio>-->
        </el-form-item>
      </el-form>
      <template #footer>
      <span class="dialog-footer">
        <el-button @click="closeAddDialog">取消</el-button>
        <el-button type="primary" @click="save"
        >确定</el-button
        >
      </span>
      </template>
    </el-dialog>
    <el-dialog
        v-model="dialogVisible2"
        title="新增用户"
        width="30%"
    ><el-form :model="form" label-width="120px">
              <el-form-item label="用户名" style="width: 80%">
                <el-input v-model="form.name" />
              </el-form-item>
      <el-form-item label="权限等级" style="width: 80%">
        <el-radio v-model="form.level" label="1" size="large">超级管理员</el-radio>
        <el-radio v-model="form.level" label="2" size="large">产品管理员</el-radio>
        <el-radio v-model="form.level" label="3" size="large">质检员</el-radio>
        <el-radio v-model="form.level" label="4" size="large">普通用户</el-radio>
        <!--          <el-radio v-model="form.level" label="4" size="large">芯片管理员</el-radio>-->
        <!--          <el-radio v-model="form.level" label="5" size="large">普通用户</el-radio>-->
      </el-form-item>
              <el-form-item label="联系电话" style="width: 80%">
                <el-input v-model="form.phone" />
              </el-form-item>
    </el-form>
      <template #footer>
      <span class="dialog-footer">
        <el-button @click="dialogVisible2 = false">取消</el-button>
        <el-button type="primary" @click="save"
        >确定</el-button
        >
      </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import request from "@/utils/request";

export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "User",
  data(){
    return{
      form:{},
      dialogVisible:false,
      dialogVisible2:false,
      currentPage:1,
      pageSize:10,
      total:10,
      search:'',
      tableData:[]
    }
  },
  created() {
    this.load()
  },
  methods:{
    open(){
      this.$notify({
        title: '权限等级说明',
        message: '1：超级管理员  2：产品管理员  3：质检员  4：普通用户',
        duration: 4500
      });
    },
    load(){
      request.get("/user?pageNum="+this.currentPage+"&pageSize="+this.pageSize+"&search="+this.search,

        // eslint-disable-next-line no-unused-vars
      ).then(res=>{
        this.tableData=res.data.records
        this.total=res.data.total
      })
    },
    add(){
        this.dialogVisible=true,
        this.form={}
    },
    adduser(){
      this.dialogVisible2=true
    },
    //清除对话框中的数据
    closeAddDialog() {
      this.dialogVisible = false   //关闭对话框
      // this.$refs.addRoleForm.resetFields();
      this.form = this.$options.data().form
    },
    save(){
      if(this.form.id){
        request.put("/user",this.form).then(res => {
          console.log(res)
          if(res.code=="0"){
            this.$message({
              type:"success",
              message:"更新成功"
            })
          }else {
            this.$message({
              type:"error",
              message:res.msg
            })
          }
          this.load()
          this.dialogVisible=false
        })
      }else{
        request.post("/user",this.form).then(res => {
          console.log(res)
          if(res.code=="0"){
            this.$message({
              type:"success",
              message:"新增成功"
            })
          }else {
            this.$message({
              type:"error",
              message:res.msg
            })
          }
          this.load()
          this.form = this.$options.data().form
          this.dialogVisible2=false
        })
      }
    },
    // eslint-disable-next-line no-unused-vars
    handleEdit(row){
      this.form = JSON.parse(JSON.stringify(row))
      this.dialogVisible = true
    },
    handleDelete(id){
      request.delete("/user/"+id).then(res=>{
        if(res.code=="0"){
          this.$message({
            type:"success",
            message:"删除成功"
          })
        }else {
          this.$message({
            type:"error",
            message:res.msg
          })
        }
        this.load()
      })
    },
    handleSizeChange(pageSize){
      this.pageSize=pageSize
      this.load()
    },
    handleCurrentChange(pageNum){
      this.currentPage=pageNum
      this.load()
    }
  }

}
</script>

<style scoped>

</style>